export default function (Component) {
      if(!Component.options.code) Component.options.code = {};
      Component.options.code['install'] = "import Stage0MenuPlugin from 'stage0-rete-menu-plugin';\n\neditor.use(Stage0MenuPlugin, {\n    menuOptions: {\n        delay: 100,\n        searchBar: false,\n        allocate(component) {\n            if (component.name == \"Add\") {\n                return false;\n            }\n            return [\"menu\", \"submenu\"];\n        },\n        items: {\n            Menu: {\n                \"Add component\": {\n                    Add: components[1]\n                },\n                Fn: () => {\n                    alert(\"Fn\");\n                }\n            }\n        }\n    },\n    dockedMenuOptions: {\n        delay: 100,\n        allocate(component) {\n            return false;\n        },\n        items: {\n            Menu: {\n                \"Add component\": {\n                    Add: components[1]\n                },\n                \"Add component 2\": {\n                    X: components[1]\n                }\n            }\n        }\n    }\n}});";
    }